import { UserService } from '../../../shared/userService/user.service';
import { Router, NavigationEnd, ActivatedRoute } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ToasterService } from '../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit, HostListener, ElementRef } from '@angular/core';
import { environment } from '../../../../environments/environment';
@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.css']
})
export class SettingComponent implements OnInit {
  user;
  url = "/admin/setting";
  data;
  pre;
  style;
  prefix = 'uploads/page/';
  imgURL;
  formGroup: FormGroup;
  constructor(
    private userService: UserService,
    private router: Router,
    private route: ActivatedRoute,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
    private sanitized: DomSanitizer
  ) { }
  ngOnInit(): void {
    this.user = this.userService.getToken()['userInfo'];
    this.getItem();
  }
  getItem() {
    this.userService.postRequest("api/admin/getSetting").subscribe(
      res => {
        if (res['result'].length > 0) this.data = res['result'][0]['code']
      },
      err => {
        this.handleError(err)
      });
  }

  saveCode() {
    this.userService.deleteLocalSetting();
    this.userService.postRequest("api/admin/updateSetting", { code: this.data }).subscribe(
      res => {
        this.handleSuccess();
        this.getItem();
      },
      err => {
        this.handleError(err)
      });
  }
  preview() {
    this.userService.setLocalSetting(this.data)
  }
  //when img click
  fileButtonClick() {
    document.getElementById('photo').click();
  }
  //when input file ok button click
  selectFile(event) {
    let reader = new FileReader();
    let ff = new FormData();
    const file = event.target.files[0];
    const filename = String(Date.now()) + '.' + file.name.split('.')[file.name.split('.').length - 1];
    ff.append('file', file, filename);
    this.userService.postRequest("api/admin/settingUpload", ff).subscribe(
      res => {
        this.handleSuccess()
        this.imgURL = "/uploads/page/" + filename;
      },
      err => {
        this.handleError(err)
      }
    );
  }
  //delete Image
  deleteImage() {
    this.userService.postRequest('api/deleteFile', { path: this.imgURL }).subscribe(
      res => {
        this.imgURL = undefined
      },
      err => {
        this.handleError(err)
      }
    );
  }
  putInMiddle(arr, key, member) {//insert memeber to in the middle of array
    const temp = [];
    for (let i = 0; i < key; i++) temp.push(arr[i])
    temp.push(member);
    for (let i = key; i < arr.length; i++) temp.push(arr[i])
    return temp;
  }
  //Toastr functions
  handleError(err) {
    if (err.status == 504) this.Toaster('error', '', 'server is not responsing.', 4000);
    else this.Toaster('error', '', err.error.message, 1500);
  }
  handleSuccess() {
    this.Toaster('success', '', 'success')
  }
  Toaster(style, msgtopic, msgContent, time = 1000) {
    this.toastr.show(style, msgtopic, msgContent, time);
  }
}