import { UserService } from '../../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild,OnInit} from '@angular/core';
import { environment } from '../../../../../environments/environment';

@Component({
  selector: 'app-camera-user-company',
  templateUrl: './camera_user_company.component.html',
  styleUrls: ['./camera_user_company.component.css']
})
export class CameraUserCompanyComponent implements OnInit {
  formGroup: FormGroup;
  date_regex = /^(0?[1-9]|1[0-2])\/(0?[1-9]|1\d|2\d|3[01])\/(19|20)\d{2}$/;
  user;
  cameralist=[];
  userlists=[];
  modalStatus=false;
  editStatus=false;
  addStatus=false;
  delKey;
  data=[];
  formData = new FormData();
  reader = new FileReader();
  fileChange = false;
  fileDish;
  fileURL;
  key;
  srcCol=['camera_id','username','available_to','created_at','created_by','status'];
  constructor(
    private userService: UserService,
    private router : Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }
  applyFilter(arr){
    if(!this.key) return true;
    for(let i=0;i<this.srcCol.length;i++){
      if(String(arr[this.srcCol[i]]).search(this.key)!=-1){
        return true;
      }
    }
    return false;
  }
  ngOnInit(): void {    
    //etc
    this.user=this.userService.getToken()['userInfo'];
    this.refresh();
    this.getItem();
  }
  add(){
    this.addStatus=true;
  }
  edit(id){
    this.editStatus=true;
    const key=this.findIndex(id,this.data);

    this.formGroup.setValue(this.data[key]);
    this.formGroup.value.created_at=this.getDateString(this.formGroup.value.created_at);
    this.formGroup.value.available_from=this.getDateString(this.formGroup.value.available_from);
    this.formGroup.value.available_to=this.getDateString(this.formGroup.value.available_to);

    this.formGroup.setValue(this.formGroup.value);
  }
  del(id){
    this.delKey = this.findIndex(id,this.data);
  }
  findIndex(id,datas) {
    for(let i=0;i<datas.length;i++){
      if(datas[i].id===id) return i;
    }
    return 0;
   }
  cancel(){
    this.refresh();
  }
  save(){
    if(this.addStatus==true){
           this.addItem();
    }
    else if(this.editStatus==true){
      // this.formGroup.value.car_number=this.findVehicleNumber(this.formGroup.value.username,this.userlists);

           this.editItem();
    }
  }
  start(id){
    const key=this.findIndex(id,this.data);

    this.editStatus=true;
    this.formGroup.setValue(this.data[key]);
    this.formGroup.value.created_at=this.getDateString(this.formGroup.value.created_at);
    this.formGroup.value.available_from=this.getDateString(this.formGroup.value.available_from);
    this.formGroup.value.available_to=this.getDateString(this.formGroup.value.available_to);
    this.formGroup.value.status='on';
    this.formGroup.setValue(this.formGroup.value);
    this.editItem();
  }
  stop(id){
    const key=this.findIndex(id,this.data);

    this.editStatus=true;
    this.formGroup.setValue(this.data[key]);
    console.log(this.formGroup.value)
    this.formGroup.value.created_at=this.getDateString(this.formGroup.value.created_at);
    this.formGroup.value.available_from=this.getDateString(this.formGroup.value.available_from);
    this.formGroup.value.available_to=this.getDateString(this.formGroup.value.available_to);
    this.formGroup.value.status='off';

    // this.formGroup.setValue(this.formGroup.value);
    this.editItem();
  }  
  addItem(){
    this.formGroup.value.created_by=this.user.username;
    this.formGroup.value.car_number=this.findVehicleNumber(this.formGroup.value.username,this.userlists);
    this.userService.postRequest("api/admin/addCameraUserCompany",{
      user:this.user,
      data:this.formGroup.value
    }).subscribe(
      res=>{
        this.Toaster('success','','success'); 
        this.refresh();
        this.getItem();
      },
      err=>{
      this.handleError(err)
      }
    );
  }
  editItem(){
    this.userService.postRequest("api/admin/editCameraUserCompany",{
      user:this.user,
      data:this.formGroup.value
    }).subscribe(
      res=>{
        this.Toaster('success','','success'); 
        this.refresh();
        this.getItem();
      },
      err=>{
      this.handleError(err)
      }
    );
  }
  deleteItem(){
    this.userService.postRequest("api/admin/delCameraUserCompany",{user:this.user,data:this.data[this.delKey]}).subscribe(
      res=>{
      this.Toaster('success','','success'); 
      this.refresh();
      this.getItem();
      },
      err=>{
      this.handleError(err)
      }
    );
  }
  getItem(){
    this.userService.postRequest("api/admin/getCameraUserCompany",{user:this.user}).subscribe(
      res=>{
      this.data=res['result'];
      this.cameralist=res['cameralist'];
      this.userlists=this.getUnique(res['userlist']);
      },
      err=>{
      }
    );
  }
  getUnique(arr){
    const temp=[];
    for(let i=0;i<arr.length;i++){
      if(this.existKey(temp,arr[i].company)) temp.push(arr[i]);
   }
   return temp;
  }
  existKey(arr,key){
    for(let i=0;i<arr.length;i++){
      if(arr[i].company===key) return false;
   }
   return true;
  }
  findVehicleNumber(key,arr){
   for(let i=0;i<arr.length;i++){
      if(arr[i].username===key) return arr[i].car_number;
   }
   return null;
  }
  refresh(){
    this.editStatus=false;
    this.addStatus=false;
    this.formData=new FormData();
    this.fileChange = false;
    this.fileDish = undefined;
    this.fileURL = undefined;
    const intenationalPhone = /^1[3|4|5|7|8][0-9]{9}$/;
    const dateRegex=/^\d{4}-\d{2}-\d{2}$/;
    this.formGroup = this._formBuilder.group({
      id:[''],
      camera_id: ['', Validators.required],
      company: ['', Validators.required],
      car_number: [''],
      created_at: [''],
      created_by: [''],
      available_from: ['',  Validators.pattern(dateRegex)],
      available_to: ['',  Validators.pattern(dateRegex)],
      status: ['', Validators.required],
    });
  }
  //toastr functions
  handleError(err){
    if(err.status==504)  this.Toaster('error', '', 'server is not responsing.',4000);
    else         this.Toaster('error', '', err.error.message,1500);
   }
  Toaster(style,msgtopic,msgContent,time=1000) {
    this.toastr.show(style, msgtopic, msgContent, time);
  }
  //for file manage
  selectFile(event) {
    this.fileChange=true;
    const mimeType = event.target.files[0].type;
    this.fileDish = event.target.files[0];
    this.reader.readAsDataURL(event.target.files[0]);
    this.reader.onload = (_event) => {
        this.fileURL = this.reader.result;
    };
    document.getElementById('photo').innerHTML=null;
  }
  fileButtonClick(){
    document.getElementById('photo').click();
  }
  //for date
  getDateString(ss){
    const d=new Date(ss);
    let dmonth=String(d.getMonth());
    let ddate=String(d.getDate());
    if(dmonth.length<2) dmonth='0'+dmonth;
    if(ddate.length<2) ddate='0'+ddate;
    return d.getFullYear()+'-'+dmonth+'-'+ddate;
  }
}
