import { UserService } from '../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit } from '@angular/core';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'app-company-user',
  templateUrl: './company_user.component.html',
  styleUrls: ['./company_user.component.css']
})
export class CompanyUserComponent implements OnInit {
  formGroup: FormGroup;
  user;
  useravatar = 'uploads/avatar/sample-1.jpg';
  defaultImage="assets/img/error/unknown.jpg";
  modalStatus = false;
  editStatus = false;
  addStatus = false;
  delKey;
  editKey;
  data = [];
  formData = new FormData();
  reader = new FileReader();
  fileChange = false;
  fileDish;
  fileURL;
  key;
  srcCol = ['username', 'created_at', 'created_by', 'available_to', 'status'];
  constructor(
    private userService: UserService,
    private router: Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }

  ngOnInit(): void {
    //etc
    this.user = this.userService.getToken()['userInfo'];
    this.refresh();
    this.getItem();
  }
  applyFilter(arr) {
    if (!this.key) return true;
    for (let i = 0; i < this.srcCol.length; i++) {
      if (String(arr[this.srcCol[i]]).search(this.key) != -1) {
        return true;
      }
    }
    return false;
  }
  add() {
    const useravatar = 'uploads/avatar/sample-' + Math.floor(Math.random() * 9) + '.jpg';
    this.useravatar = useravatar;
    this.addStatus = true;
  }
  edit(id) {
    this.editKey = this.findIndex(id, this.data);
    this.editStatus = true;
    this.formGroup.setValue(this.data[this.editKey]);
    this.formGroup.value.created_at = this.getDateString(this.formGroup.value.created_at);
    this.formGroup.value.available_from = this.getDateString(this.formGroup.value.available_from);
    this.formGroup.value.available_to = this.getDateString(this.formGroup.value.available_to);
    this.formGroup.setValue(this.formGroup.value);
  }
  del(id) {
    this.delKey = this.findIndex(id, this.data);
  }
  findIndex(id, datas) {
    for (let i = 0; i < datas.length; i++) {
      if (datas[i].id === id) return i;
    }
    return 0;
  }
  cancel() {
    this.refresh();
  }
  save() {
    if (this.addStatus == true) {
      if (this.fileChange) {
        const ns = this.formGroup.value.username;
        const filename = ns + '.' + this.fileDish.name.split('.')[this.fileDish.name.split('.').length - 1];;
        this.formData.append('file', this.fileDish, filename);
        this.userService.postRequest("api/uploadAvatar",this.formData).subscribe(
          res => {
            this.formGroup.value.avatar = res['path'];
            this.addItem();
          },
          err => {
            this.handleError(err)
          }
        );
      } else {
        this.formGroup.value.avatar = this.useravatar;
        this.addItem();
      }
    }
    else if (this.editStatus == true) {
      if (this.fileChange) {
        const ns = this.formGroup.value.username;
        const filename = ns + '.' + this.fileDish.name.split('.')[this.fileDish.name.split('.').length - 1];;
        this.formData.append('file', this.fileDish, filename);
        this.userService.postRequest("api/uploadAvatar",this.formData).subscribe(
          res => {
            this.formGroup.value.avatar = res['path'];
            this.editItem();
          },
          err => {
            this.handleError(err)
          }
        );
      } else {
        this.editItem();
      }
    }
  }
  addItem() {
    this.formGroup.value.created_by = this.user.username;
    this.userService.postRequest("api/admin/addCompany", {
      user: this.user,
      data: this.formGroup.value
    }).subscribe(
      res => {
        this.Toaster('success', '', 'success')
        this.refresh();
        this.getItem();
      },
      err => {
        this.handleError(err)
      }
    );
  }
  editItem() {
    this.formGroup.value.created_by = this.user.username;
    this.userService.postRequest("api/admin/editCompany", {
      user: this.user,
      data: this.formGroup.value
    }).subscribe(
      res => {
        this.refresh();
        this.getItem();
        this.Toaster('success', '', 'success')
      },
      err => {
        this.handleError(err)
      }
    );
  }
  deleteItem() {
    this.userService.postRequest("api/admin/delCompany", { user: this.user, data: this.data[this.delKey] }).subscribe(
      res => {
        this.Toaster('success', '', 'success')
        this.refresh();
        this.getItem();
      },
      err => {
        this.handleError(err)
      }
    );
  }
  getItem() {
    this.userService.postRequest("api/admin/getCompany", { user: this.user }).subscribe(
      res => {
        this.data = res['result'];
      },
      err => {
      }
    );
  }
  refresh() {
    this.editStatus = false;
    this.addStatus = false;
    this.formData = new FormData();
    this.fileChange = false;
    this.fileDish = undefined;
    this.fileURL = undefined;
    const accountRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z0-9]{11}$/;
    const intenationalPhone = /^1[3|4|5|7|8][0-9]{9}$/;
    const dateRegex = /^\d{4}-\d{2}-\d{2}$/;
    this.formGroup = this._formBuilder.group({
      id: [''],
      username: ['company1234', Validators.pattern(accountRegex)],
      password: ['12345678', Validators.required],
      company: ['trackingcompany', Validators.required],
      contact: ['trackingcompany@mail.com', Validators.email],
      mail_box: ['johnid123@mail.com', Validators.email],
      phone: ['18654875653', Validators.pattern(intenationalPhone)],
      car_number: [''],
      avatar: [''],
      address: ['chonjin 23 street', Validators.required],
      license_number: ['xxxx-xxxx-xxxx', Validators.required],
      tax_number: ['xxxx-xxxx-xxxx', Validators.required],
      bank_account: ['foe234@fkc', Validators.required],
      created_at: [''],
      created_by: [''],
      available_from: ['2000-01-01', Validators.pattern(dateRegex)],
      available_to: ['2000-01-01', Validators.pattern(dateRegex)],
      status: ['on', Validators.required],
      role: ['admin']
    });
  }
  //for file manage
  selectFile(event) {
    this.fileChange = true;
    const mimeType = event.target.files[0].type;
    this.fileDish = event.target.files[0];
    this.reader.readAsDataURL(event.target.files[0]);
    this.reader.onload = (_event) => {
      this.fileURL = this.reader.result;
    };
    event.target.value = "";
  }
  fileButtonClick() {
    document.getElementById('photo').click();
  }
  //for image
  getAvatarSource() {
    let src;
    if (this.fileChange) src = this.fileURL;
    else if (this.editStatus) src = this.formGroup.value.avatar;
    else src = this.useravatar;
    return src;
  }
  //for date
  getDateString(ss) {
    const d = new Date(ss);
    let dmonth = String(d.getMonth());
    let ddate = String(d.getDate());
    if (dmonth.length < 2) dmonth = '0' + dmonth;
    if (ddate.length < 2) ddate = '0' + ddate;
    return d.getFullYear() + '-' + dmonth + '-' + ddate;
  }
  //toastr functions
  handleError(err) {
    if (err.status == 504) this.Toaster('error', '', 'server is not responsing.', 4000);
    else this.Toaster('error', '', err.error.message, 1500);
  }
  Toaster(style, msgtopic, msgContent, time = 1000) {
    this.toastr.show(style, msgtopic, msgContent, time);
  }
}
