import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from '../../shared/userService/auth/auth.guard';
import { CompanyUserComponent } from './company/company_user.component';
import { SystemUserComponent } from './system/system_user.component';
import { IndividualUserComponent } from './individual/individual_user.component';
import { CameraRegisterComponent } from './device/camera_register/camera_register.component';
import { CameraUserCompanyComponent } from './device/camera_user_company/camera_user_company.component';
import { CameraUserIndividualComponent } from './device/camera_user_individual/camera_user_individual.component';
import { SettingComponent } from './setting/setting.component';
export const SystemRoutes: Routes = [
{
    path: '', 
    children: [
        { path: '', redirectTo: 'setting', pathMatch: 'full' },
        {path: 'system_user', canActivate:[AuthGuard], data:{role:['super']}, component: SystemUserComponent},
        {path: 'setting', canActivate:[AuthGuard], data:{role:['super','system']}, component: SettingComponent},
        {path: 'company_user', canActivate:[AuthGuard], data:{role:['super','system']}, component: CompanyUserComponent},
        {path: 'individual_user', canActivate:[AuthGuard], data:{role:['super','system']}, component: IndividualUserComponent},
        {path: 'camera_register', canActivate:[AuthGuard], data:{role:['super','system']}, component: CameraRegisterComponent},
        {path: 'camera_user_individual', canActivate:[AuthGuard], data:{role:['super','system']}, component: CameraUserIndividualComponent},
        {path: 'camera_user_company', canActivate:[AuthGuard], data:{role:['super','system']}, component: CameraUserCompanyComponent},
    ]
},
];

